import {Routes} from '@angular/router';

import {ChargeInstallationListComponent} from "./charge_installation.list/charge_installation.list.component";

export const ChargeInstallationRoutes:Routes = [{
  path: '',
  redirectTo: 'list',
  pathMatch: 'full',
}, {
  path: '',
  children: [
    {path: 'list', component: ChargeInstallationListComponent},
  ]
}];



