import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FileUploadModule } from 'ng2-file-upload/ng2-file-upload';
import { WizardModule } from 'ng2-archwizard';
import { ChargeInstallationRoutes } from './charge_installation.routing';
import { ChargeInstallationService } from "./service/charge_installation.service";
import { ChargeInstallationListComponent } from "./charge_installation.list/charge_installation.list.component";
import { DataTableModule } from "angular2-datatable";
import { MaterielService } from "../admin/materiel/service/materiel.service";
import { InstallationService } from '../installation/service/installation.service';
import { ReparationService } from '../reparation/service/reparation.service';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    DataTableModule,
    WizardModule,
    RouterModule.forChild(ChargeInstallationRoutes)
  ],
  declarations: [
    ChargeInstallationListComponent,
  ],
  providers: [
    ChargeInstallationService,
  ],
})

export class ChargeInstallationModule {
}



