import { Injectable } from "@angular/core";
import { Response, ResponseContentType } from "@angular/http";
import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import { SettingsService } from "../../core/settings/settings.service";
import { AuthHttpService } from "../../auth/auth-http.service";
import { ChargeInstallation } from "../metier/charge_installation.metier";
import { Installation } from "../../installation/metier/installation.metier";

@Injectable()
export class ChargeInstallationService {

  constructor(private settings: SettingsService,
    private authHttp: AuthHttpService) {
  }

  saveListChargeInstallation(chargeInstallationList: Array<ChargeInstallation>): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/chargeInstallation/saveList`, JSON.stringify(chargeInstallationList));
  }

  getChargeInstallationListByInstallation(installation_id: number): Observable<ChargeInstallation[]> {
    return this.authHttp.get(this.settings.baseUrl + `/chargeInstallation/listByInstallation/${installation_id}`)
      .map((res: Response) => res.json());
  }

}



