"use strict";

import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { fadeInAnimation } from "../../core/route-animation/route.animation";
import { ChargeInstallation } from "../metier/charge_installation.metier";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { BaseComponent } from "../../core/component/base-component";
import { ChargeInstallationService } from "../service/charge_installation.service";
import { PageTitleService } from "../../core/page-title/page-title.service";
import { Materiel } from "../../admin/materiel/metier/materiel.metier";
import { MaterielService } from "../../admin/materiel/service/materiel.service";
import { InstallationService } from '../../installation/service/installation.service';
import { Installation } from '../../installation/metier/installation.metier';
import { ActivatedRoute } from '@angular/router';
import { CustomValidators } from 'ng2-validation';
import { Reparation } from '../../reparation/metier/reparation.metier';
import { ReparationService } from '../../reparation/service/reparation.service';

@Component({
  selector: 'ms-chargeInstallation-list',
  templateUrl: './charge_installation.list.component.html',
  styleUrls: ['./charge_installation.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})
export class ChargeInstallationListComponent extends BaseComponent implements OnInit {

  private chargeInstallationList: Array<ChargeInstallation> = [];
  private totalChargeInstallation: number;


  constructor(private fb: FormBuilder,
    private chargeInstallationService: ChargeInstallationService,
    private pageTitleService: PageTitleService,
    private route: ActivatedRoute) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("CHARGE INSTALLATION");
    this.loadInstallation();
  }


  private loadInstallation() {
    this.route.queryParams.subscribe(params => {
      let installation_id = params['installation_id'];
      this.loadChargeInstallationList(installation_id);
    }, error => {
      this.showError(error.status, error.json().message);
    })
  }


  private loadChargeInstallationList(installation_id) {
    this.chargeInstallationService.getChargeInstallationListByInstallation(installation_id)
      .subscribe(chargeInstallationList => {
        this.chargeInstallationList = chargeInstallationList;
        this.calculeTotal();
      }, error => {
        this.showError(error.status, error.json().message);
      });
  }

  private calculeTotal() {
    this.totalChargeInstallation = 0;
    for (let chargeInstallation of this.chargeInstallationList) {
      this.totalChargeInstallation += (chargeInstallation.montant);
    }
  }

  private saveChargeInstallation() {
    this.chargeInstallationService.saveListChargeInstallation(this.chargeInstallationList)
      .subscribe(() => {
        this.showInfo("Enregistrement effectu√© avec succ√®s");
        this.router.navigate(["/installation/list/"]);
      }, error => this.showError(error.status, error.json().message));
  }

  private cancel() {
    this.router.navigate(["/installation/list/"]);
  }

}



